<template >

  <section class="login">
    <div class="container-scroller">
      <div class="container-fluid page-body-wrapper full-page-wrapper">
        <div class="content-wrapper d-flex align-items-center auth">
          <div class="row w-100 flex-grow">
            <div class="col-xl-4 col-lg-6 mx-auto">
              <div class="auth-form-light text-left p-5">
                <div class="brand-logo d-flex justify-content-center">
                  <img src="../../../assets/logo.png" height="120" width="0">
                </div>
                <h4>Welcome to eCrack</h4>
                <h6 class="font-weight-light">Enter your username and password to log in</h6>
                <form class="pt-3">
                  <div class="form-group">
                    <input v-model="userName" type="" class="form-control form-control-lg text-center" id="exampleInputEmail1" placeholder="user name">
                  </div>
                  <div class="form-group">
                    <input v-model="pass" type="" class="form-control form-control-lg text-center" id="exampleInputPassword1" placeholder="password">
                  </div>
                  <div class="mt-3">
                    <div :disabled="!!loading" class="btn btn-block btn-gradient-primary btn-lg font-weight-medium auth-form-btn" v-on:click="loginTap">
                      <span
                          v-if="loading"
                          class="spinner-border spinner-border-sm mb-1"
                          role="status"
                          aria-hidden="true"
                      ></span>
                      <div v-else>
                        <h4>Log in</h4>
                      </div>
                    </div>
                  </div>

                  <div v-show="errored" class="alert alert-danger mt-2">
                    <!--                    <strong style="font-family: ubuntu-light;">خطا!</strong>-->
                    <span style="font-family: ubuntu-light;">{{this.errorMessage}}</span>
                  </div>

                  <div v-show="success" class="alert alert-success mt-2">
                    <span style="font-family: ubuntu-light;">You are logged in</span>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <!-- content-wrapper ends -->
      </div>
      <!-- page-body-wrapper ends -->
    </div>
  </section>
</template>


<script>

import api from '../../../const/api'

export default {
  name: 'login',
  data() {
    return {
      userName: null,
      pass: null,
      loading: false,
      errored: false,
      errorMessage: "",
      success: false
    };
  },
  methods: {
    loginTap: function() {


        if (!this.userName) {
          this.errored = true
          this.errorMessage = "Please enter your username"
          setTimeout( () => {
            this.errored = false
          }, 3000 )
          return;
        }

        if (!this.pass) {
          this.errored = true
          this.errorMessage = "Please enter your password"
          setTimeout( () => {
            this.errored = false
          }, 3000 )
          return;
        }

        this.loading = true;
        // const body = { username: this.userName, pass: this.pass };

      let formData = new FormData();

      formData.append('username', this.userName);
      formData.append('pass', this.pass);

        this.axios
            .post(api.baseURL + '/panel/login.php', formData)
            .then(response => {

              // eslint-disable-next-line no-console
              console.log(response);
              this.loading = false;
              if (response.status == 200 && response.data.state == 'done') {
                localStorage.token = 'verified'
                this.success = true

                setTimeout( () => {
                  this.success = false
                  this.$router.push({path: '/'});
                }, 1000 )
              } else {
                this.errored = true
                this.errorMessage ='Wrong username or password'
              }

            })
            .catch(error => {
              // eslint-disable-next-line no-console
              console.log(error)
              this.loading = false
              this.errored = true
              this.errorMessage = 'Please try again'
            });

    }
  }
}
</script>
